<template>
  <div class="bg-light pt-6">
    <loading :active.sync="isLoading" />
    <!--  -->
    <div class="container">
      <div class="row">
        <div class="col-md-2 pb-3 pb-md-0">
          <img class="img-fluid" src="@/assets/img/step_2.png" alt />
        </div>
        <div class="col-md-10">
          <h3></h3>
          <ul class="d-flex justify-content-start list-unstyled">
            <li class="font-weight-bold"></li>
            <li>
              <i class="fa fa-angle-right mx-2" aria-hidden="true"></i>
            </li>
            <li class="text-primary h6"></li>
            <li>
              <i class="fa fa-angle-right mx-2" aria-hidden="true"></i>
            </li>
            <li class="text-muted"></li>
          </ul>
        </div>
      </div>
    </div>

    <div class="order container pt-2 pb-5">
      <div class="row">
        <!--  -->
        <div class="col-md-12 col-lg-5">
          <table class="table mt-2 shadow-sm border border-dark table-striped">
            <thead class="thead-dark">
              <th scope="col" class="text-center" width="60px"></th>
              <th class="text-center"></th>
              <th width="30px" class="text-right"></th>
            </thead>
            <tbody>
              <tr v-for="(item,index) in cart.carts" :key="index">
                <th scope="row" class="text-center align-middle">{{index + 1}}</th>
                <td>
                  {{ item.product.title }} {{ item.qty }} 
                  <div class="text-secondary" v-if="item.coupon"></div>
                </td>
                <td class="align-middle text-right">
                  <small>NT</small>
                  {{ item.final_total | currency }}
                </td>
              </tr>
            </tbody>
            <tfoot class="table-dark">
              <tr>
                <td colspan="2" class="text-right"></td>
                <td class="text-right" v-if="cart.final_total === cart.total">
                  <strong class="h5">
                    <small>NT</small>
                    {{ cart.total | currency }}
                  </strong>
                </td>
                <td class="text-right" v-if="cart.final_total !== cart.total">
                  <s class="h5">
                    <small>NT</small>
                    {{ cart.total | currency }}
                  </s>
                </td>
              </tr>
              <!--  "cart.final_total  cart.total  -->
              <tr class="bg-light" v-if="cart.final_total !== cart.total">
                <td colspan="2" class="text-right text-secondary"></td>
                <td class="text-right text-secondary">
                  <strong class="h5">
                    <small>NT</small>
                    {{ cart.final_total | currency }}
                  </strong>
                </td>
              </tr>
            </tfoot>
          </table>
          <div class="input-group">
            <input
              type="text"
              class="form-control form-control-sm rounded-0"
              placeholder=" 1111 "
              v-model="coupon_code"
            />
            <div class="input-group-append">
              <button
                class="btn btn-sm btn-primary rounded-0"
                @click="addCouponCode"
                type="button"
              ></button>
            </div>
          </div>

          <div class="text-right my-3">
            <router-link class="btn btn-outline-dark rounded-0 btn-sm" to="/cart">
              <i class="fa fa-long-arrow-left" aria-hidden="true"></i> 
            </router-link>
          </div>
        </div>
        <!-- -->
        <div class="col-md-12 col-lg-7 mb-3">
          <form @submit.prevent="createOrder()">
            <h2></h2>
            <div class="form-row">
              <!--  -->
              <div class="form-group col-md-6">
                <label for="username"></label>
                <input
                  type="text"
                  class="form-control"
                  :class="{ 'is-invalid': errors.has('name') }"
                  name="name"
                  id="username"
                  v-model="form.user.name"
                  v-validate="'required'"
                  placeholder=""
                />
                <span class="text-danger" v-if="errors.has('name')"></span>
              </div>
              <!--  -->
              <div class="form-group col-md-6">
                <label for="usertel"></label>
                <input
                  type="tel"
                  class="form-control"
                  :class="{ 'is-invalid': errors.has('tel') }"
                  name="tel"
                  id="usertel"
                  v-model="form.user.tel"
                  v-validate="'required'"
                  placeholder=""
                />
                <span class="text-danger" v-if="errors.has('tel')"></span>
              </div>
            </div>
            <!-- email -->
            <div class="form-group">
              <label for="useremail">Email</label>
              <input
                type="email"
                class="form-control"
                :class="{ 'is-invalid': errors.has('email') }"
                name="email"
                id="useremail"
                v-model="form.user.email"
                v-validate="'required|email'"
                placeholder=" Email"
              />
              <span class="text-danger" v-if="errors.has('email')">{{ errors.first('email') }}</span>
            </div>
            <!--  -->
            <div class="form-group">
              <label for="useraddress"></label>
              <input
                type="text"
                class="form-control"
                :class="{ 'is-invalid': errors.has('address') }"
                name="address"
                id="useraddress"
                v-model="form.user.address"
                v-validate="'required'"
                placeholder=""
              />
              <span class="text-danger" v-if="errors.has('address')"></span>
            </div>
            <!--  -->
            <div class="form-group">
              <label for="comment"></label>
              <textarea
                name
                id="comment"
                class="form-control"
                cols="10"
                rows="5"
                v-model="form.message"
              ></textarea>
            </div>
            <div class="text-right">
              <button class="btn btn-primary rounded-0">
                
                <i class="fa fa-long-arrow-right" aria-hidden="true"></i>
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery';
import { mapActions } from 'vuex';

export default {
  data() {
    return {
      form: {
        user: {
          name: '',
          email: '',
          tel: '',
          address: '',
        },
        message: '',
      },
      coupon_code: '',
    };
  },
  methods: {
    ...mapActions(['a_updateMessage']),

    createOrder() {
      const vm = this;
      const api = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_CUSTOMPATH}/order`;
      const order = vm.form;
      this.$validator.validate().then(result => {
        if (result) {
          this.$http.post(api, { data: order }).then(response => {
            // console.log(response.data);
            if (response.data.success) {
              vm.$router.push(`/checkout/${response.data.orderId}`);
            }
          });
        } else {
          // console.log(response.data);
          this.$store.dispatch('a_updateMessage', { message: '', status: 'danger' });
        }
      });
    },
    getCart() {
      this.$store.dispatch('a_getCart');
    },
    addCouponCode() {
      const vm = this;
      const api = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_CUSTOMPATH}/coupon`;
      const coupon = {
        code: vm.coupon_code,
      };
      vm.$store.dispatch('a_updateLoading', true);
      this.$http.post(api, { data: coupon }).then(response => {
        if (response.data.success) {
          this.$store.dispatch('a_updateMessage', { message: '', status: 'primary' });
        } else {
          this.$store.dispatch('a_updateMessage', { message: '', status: 'danger' });
        }
        // console.log(response.data);
        vm.getCart(); // 
        vm.$store.dispatch('a_updateLoading', false);
      });
    },
  },
  computed: {
    isLoading() {
      return this.$store.state.isLoading;
    },
    cart() {
      return this.$store.state.cart;
    },
  },
  created() {
    const vm = this;
    vm.getCart();
  },
};
</script>

<style scoped lang="scss">
.order {
  min-height: 60vh;
}
</style>